* {
  margin: 0;
  border: 0;
  padding: 0;
  outline: none;
  border: none;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}

:root {
  --font-roboto: "Roboto Slab", serif;
  --font-raleway: "Raleway", sans-serif;
}

body {
  font-family: var(--font-roboto);
  /* background-color: #212426; */
}

.App {
  background: #fff;
  position: relative;
}

.header {
  grid-row: 1 / 1;
  grid-column: 1 /-1;
  background: blue;
}

.advert {
  grid-row: 2 / -1;
  grid-column: 12 / -1;
  background-color: rgb(45, 118, 147);
}
.message {
  position: fixed;
  bottom: 0;
  right: 0;
  height: 300px;
  width: 225px;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: -2px 4px 4px 2px #ddd;
}

.message {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  overflow: hidden;
}
.message-header {
  width: 100%;
  background: #04993f;
  text-align: center;
  color: #fff;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  padding: 0.5rem;
}
.input-text {
  position: absolute;
  bottom: 2rem;
  left: 0;
  background: #fff;
  border: 1px solid #ddd;
  width: 100%;
  text-align: left;
  border-right: 0;
}
.message button {
  position: absolute;
  bottom: 2rem;
  right: 0;
  font-size: 2rem;

  background-color: #04993f;
  padding: 0.8rem 1.2rem;
  border-bottom-right-radius: 12px;
  border-top-right-radius: 12px;
}

.messages {
  /* width: 100%;
  height: 100%; */
  position: relative;
}
.m-left {
  position: absolute;
  top: 2rem;
  right: 0.5rem;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ddd;
  border-radius: 12px;
}
.m-right {
  position: absolute;
  top: 5rem;
  left: 0.125rem;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ddd;
  border-radius: 12px;
}
.m-down--left {
  position: absolute;
  top: 8.8rem;
  right: 0.125rem;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ddd;
  border-radius: 12px;
}
.tabs {
  grid-row: 2 / span 1;
  grid-column: 6 / span 5;
  /* background-color: brown; */

  text-align: center;
}
.display-tabs {
  /* width: 90%; */
  margin: 0 auto;
  box-shadow: 0 2px 4px 0 #000508;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.display-tabs a {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 1.5rem;
  padding: 1rem;
  color: #000405;
  text-decoration: none;
  font-weight: 500;
}
.comment {
  grid-row: 11 / 15;
  grid-column: 6 / span 5;
  background: #612356;
}

.container {
  /* width: 80%;
  margin: 0 auto; */
  display: grid;
  grid-template-rows: 35vh repeat(14, 8rem);
  /* grid-template-rows: 35vh repeat(12, 8rem) 40vw repeat(5, min-content); */
  grid-template-columns:
    [sidebar-start] 8rem [sidebar-end full-start] 1fr [center-start] repeat(
      12,
      [col-start] minmax(min-content, 15rem) [col-end]
    )
    [center-end] 1fr [full-end];
}

/* -----------HEADER STYLING----------- */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(
    98deg,
    rgba(0, 0, 0, 0.9),
    #fff,
    #04993f,
    #fff,
    rgba(118, 5, 5, 0.881)
  );
  padding: 2rem;
}
.header-link {
  font-size: 2rem;
  text-decoration: underline;
  color: #fff;
  font-weight: 500;
}
.logo {
  position: relative;
  font-size: 2rem;
  color: #fff;
  text-shadow: -1px 2px 2px 2px #ddd;

  font-family: sans-serif;
  text-align: left;
  text-transform: uppercase;
}
.header-signUp .btn:nth-child(2) {
  margin-left: 1.3rem;
}
.first {
  text-shadow: -1px 2px 2px 2px #ddd;
  margin-left: -3rem;
}
.sec {
  text-shadow: -1px 2px 2px 2px #ddd;
  margin: 0;
}
.thrd {
  margin-left: 4rem;
  text-shadow: -1px 2px 2px 2px #ddd;
}
/* --------------Sidebar------------ */
.sidebar {
  font-family: sans-serif;
  grid-row: 3 / span 6;
  grid-column: 3 / span 2;
  box-shadow: -1px 2px 2px 1px #ddd;
}
.sides {
  padding: 2rem 0;
  /* line-height: 1.9; */
}
.sides-link {
  text-decoration: none;
  display: block;
  width: 100%;
  font-size: 2rem;
  text-transform: capitalize;
  padding: 0 0.4rem 1.5rem;
  margin: 1rem 0 2rem;
  border-bottom: 1px solid #ddd;
  border-width: 80%;
  display: flex;
  color: #000508;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
/* .sides-link::before {
  position: absolute;
  bottom: -1.4rem;
  right: -1rem;
  content: "2";
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: red;
  color: #fff;
  font-size: 1rem;

  display: flex;
  align-items: center;
  justify-content: center;
} */
.drop-down {
  font-size: 1rem;
  cursor: pointer;
}

.btn {
  padding: 0.5rem 2.4rem;
  font-size: 1.7rem;
  text-transform: capitalize;
  font-weight: 700;
  border-radius: 0.6rem;
  background: #eeddae;
  color: #612356;
  cursor: pointer;
}
.btn-login {
  transition: all 0.3s;
  border: 1px solid transparent;
}
.btn:nth-child(2):hover {
  background: #081f2f;
  border: 1px solid rgb(191, 184, 184);
}
/* ------------Content----------- */
.content {
  /* width: 85%;
  margin: 0 auto;
  padding: 2rem; */
  grid-row: 3 / span 7;
  grid-column: 6 / span 5;
  background: rgb(252, 252, 252);
}
.content-tabs {
  margin: 0 0rem 2rem;
  display: flex;
  border: 1px solid #111;
  padding: 1rem;
  justify-content: space-between;
  align-items: center;
  border-radius: 6px;
  /* background-color: #06023e; */
}

.c-btn {
  width: 100%;
  height: auto;
  padding: 1rem;
  font-size: 1.4rem;
  font-weight: 600;
  border-radius: 0.6rem;
  background-color: #054517;
  color: #fff;
  border: 1px solid #fff;
}
.active,
.c-btn:hover {
  color: #fff;
  background: #000508;
  border: 1px solid #06023e;
}

.blogs {
  padding: 2rem;
  margin: 2rem 0;
  border-radius: 0.6rem;
  box-shadow: -1px 2px 4px 1px #ddd;
}
.blog-heading {
  display: flex;
  justify-content: space-between;
}
.blog-heading h1 {
  font-weight: 700;
  font-size: 1.6rem;
  color: #06023e;
}
.blog-heading i {
  font-size: 1.2rem;
  margin: 1.3rem 0;
}
.blog-date {
  font-size: 1.4rem;
}
.blog-text p {
  text-align: left;
  font-size: 1.6rem;
  line-height: 1.6;
  color: #000508;
}
.i-text {
  font-size: 1rem;
  color: #054517;
}
/* ------------Footer-------------- */
.footer {
  box-shadow: -1px 2px 2px 2px #ddd;
  display: flex;
  border-radius: 6px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  /* background-color: #d7d52b; */
  z-index: 999;
  grid-row: 10 / 13;
  grid-column: 2 / span 3;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  padding: 1.2rem;
}
p,
.copyright {
  font-size: 1.6rem;
  font-weight: 400;
  text-align: center;
}
.social-links a {
  margin-left: 0.8rem;
  font-weight: 500;
  color: #000508;
  text-decoration: none;
  text-transform: capitalize;
}

/* --------------Comment----------- */
.comment {
  background-color: #fff;
  box-shadow: -1px 2px 2px 2px #ddd;
  padding: 1rem;
  border-radius: 6px;
}
.form-container {
  width: 100%;
}
textarea {
  width: 100%;
  margin-bottom: 1rem;
  padding: 1rem 2rem;
  text-transform: capitalize;
  border: 1px solid #ddd;
  display: block;
  height: 100px;
  resize: none;
  font-size: 1.4rem;
  color: #999;
}

input {
  padding: 1.3rem;
  background: #094e80;
  color: #fff;
  border-radius: 0.6rem;
  font-size: 1.4rem;
  font-weight: 700;
  text-align: right;
  text-transform: capitalize;
}
.comments {
  border: 1px solid #ddd;
  margin: 1rem 0;
  padding: 1rem;
}
.comment-p {
  margin-bottom: 0.8rem;
  text-align: left;
  font-size: 1.2rem;
  font-weight: 500;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  width: 50%;
}
.action .ct-btn {
  padding: 0 1rem;
  background: transparent;
  text-transform: capitalize;
  border: 1px solid #ddd;
  padding: 0.5rem;
  margin-top: 1.5rem;
}

.commenter {
  display: flex;
  align-items: center;
}
img {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  margin-right: 1rem;
}
.commenter i {
  text-align: left;
  font-size: 1rem;
  color: #081f2f;
}
.commenter p {
  font-size: 1rem;
  text-align: left;
}

/* --------Advert-------- */
.advert {
  display: flex;
  flex-direction: column;
  padding: 1.3rem;
  height: 500px;
  background: #fff;
  box-shadow: -2px 4px 4px 2px #ddd;
  align-items: center;
  justify-content: space-around;
}
.personal-acc {
  padding: 1.1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 6px;
  line-height: 1.6;
  width: 100%;
  box-shadow: -1px 2px 2px 2px #ddd;
}
.personal-acc p {
  font-size: 1.2rem;
  font-weight: 300;
}
.img {
  height: 7rem;
  width: 7rem;
  background-image: linear-gradient(
    90deg,
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0.3),
    rgba(0, 0, 0, 0.6)
  );
  border-radius: 50%;
  position: relative;
}
.img::after {
  content: "ðŸ“·";
  position: absolute;
  bottom: 0;
  right: -1rem;
  /* width: 3rem;
  height: 3rem; */
  font-size: 2rem;
}
.docs {
  display: flex;
  margin: 1rem 0;
  border: 1px solid #ddd;
  padding: 0 1rem;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}
.uni-advert {
  padding: 3rem;
  background-image: linear-gradient(
    rgb(0, 0, 0) 10%,
    rgb(0, 0, 0),
    rgb(255, 255, 255),
    rgba(12, 111, 22, 0.7),
    rgba(12, 111, 22, 0.7),
    rgba(255, 255, 255),
    rgba(169, 12, 17, 0.495),
    rgba(118, 5, 5, 0.7)
  );
  width: 10rem;
  height: 10rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: #fff;
  font-weight: 700;
  font-size: 2rem;
  animation: rotateUP 1s infinite;
}
.rotate {
  text-align: center;
  /* animation: rotateMe 1s infinite; */
}
.chat {
  display: flex;
  flex-wrap: wrap;
}
.chat h3 {
  cursor: pointer;
  margin: 0 0.4rem;
  color: blue;
  font-weight: 500;
}
@keyframes rotateMe {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotateUP {
  0% {
    transform: rotate(-360deg);
  }

  100% {
    transform: rotate(0deg);
  }
}
